'use strict';

/**
* Compute the absolute value and the phase of a single-precision complex floating-point number.
*
* @module @stdlib/math-base-special-cpolarf
*
* @example
* import Complex64 from '@stdlib/complex-float32-ctor';
* import cpolarf from '@stdlib/math-base-special-cpolarf';
*
* var o = cpolarf( new Complex64( 5.0, 3.0 ) );
* // returns [ ~5.83, ~0.5404 ]
*
* @example
* import Complex64 from '@stdlib/complex-float32-ctor';
* import Float32Array from '@stdlib/array-float32';
* import cpolarf from '@stdlib/math-base-special-cpolarf';
*
* var out = new Float32Array( 2 );
*
* var v = cpolarf.assign( new Complex64( 5.0, 3.0 ), out, 1, 0 );
* // returns <Float32Array>[ ~5.83, ~0.5404 ]
*
* var bool = ( v === out );
* // returns true
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import assign from './assign.js';
import main from './main.js';


// MAIN //

setReadOnly( main, 'assign', assign );


// EXPORTS //

export default main;
